@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  box-sizing: border-box;
  font-family: 'Nanum Gothic', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{
  background-color: #333;
  min-height: 100vh;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
header{
  margin: 0 auto 30px;
  display: flex;
  justify-content: center;
}
.header--item{
  font-size: 2rem;
  color: #f0f0f0;
}
.header--item:not(:last-child){
  margin-right: 20px;
}
.container{
  min-width: auto;
  display: flex;
  padding: 30px;
  border-radius: 8px;
  background-color: #334059;
}

.clock-wrapper{
  width: 200px;
  height: 200px;
  padding: 20px;
  box-sizing: content-box;
}
.clock{
  box-sizing: content-box;
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #222;
  border: 4px solid #000;
  border-radius: 50%;
  -webkit-box-shadow: 11px 14px 23px 10px rgba(52, 52, 52, 0.56);
  -moz-box-shadow: 11px 14px 23px 10px rgba(52, 52, 52, 0.56);
  box-shadow: 11px 14px 23px 10px rgba(52, 52, 52, 0.56);
}
.clock-arrow{
  background-color: #ffd138;
  height: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
  transform-origin: left;

}
.hours-arrow{
  width: 45px;
}
.min-arrow{
  width: 55px;
}
.sec-arrow{
  width: 65px;
  height: 1px;
}
.t-word{
  position: absolute;
  --offset: 16px;
  color: #fff;
}
.t15{
  top: 50%;
  right: var(--offset);
  transform: translateY(-50%);
}
.t30{
  bottom: var(--offset);
  right: 50%;
  transform: translateX(50%);
}
.t45{
  top: 50%;
  left: var(--offset);
  transform: translateY(-50%);
}
.t60{
  top: var(--offset);
  right: 50%;
  transform: translateX(50%);
}
.clock-line{
  height: 1px;
  width: 97px;
  position: absolute;
  top: 50%;
  right: 50%;
  transform-origin: right;
}
.clock-line::after{
  position: absolute;
  content: "";
  width: 8px;
  height: 1px;
  background-color: #fff;
}
/*//////////////////////////////////////////*/
.section-numbers{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 40px;
}
.numbers{
  margin-top: 40px;
}
.number{
  display: inline;
  position: relative;
  padding: 5px;
  color: #ffeca7;
  font-size: 1.5rem;
  border: 1px solid #ffd138;
  border-radius: 10px;
  -webkit-box-shadow: 2px 4px 8px 0px rgba(255, 209, 56, 0.2);
  -moz-box-shadow: 2px 4px 8px 0px rgba(255, 209, 56, 0.2);
  box-shadow: 2px 4px 8px 0px rgba(255, 209, 56, 0.2);
}
.number:not(:last-child){
  margin-right: 20px;
}
.number span{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  user-select: none;
  color: #ffd138;
}
.number span:first-child{
  top: -70%;
}
.number span:last-child{
  bottom: -70%;
}
.btns{
  width: 160px;
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.btns button{
  padding: 8px 16px;
  margin-bottom: 10px;
  text-transform: uppercase;
  color: #ffeca7;
  background-color: transparent;
  border:1px solid #ffd138;
  border-radius: 5px;
  cursor: pointer;
  -webkit-box-shadow: 2px 4px 8px 0px rgba(255, 209, 56, 0.2);
  -moz-box-shadow: 2px 4px 8px 0px rgba(255, 209, 56, 0.2);
  box-shadow: 2px 4px 8px 0px rgba(255, 209, 56, 0.2);
  transition: .2s;
}
.btns button:hover{
  background-color: rgba(255,255,255,.1);
  color: #fff438;
}
.list-section{
  display: flex;
  flex-direction: column;
  height: 250px;
  margin-left: 30px;
  padding-right: 10px;
  color: #fff438;
  font-size: 1.3rem;
  overflow: auto;
}
.list-section span{
  margin-left: 16px;
}
.list-section::-webkit-scrollbar {
  width: 2px;               /* ширина scrollbar */
}
.list-section::-webkit-scrollbar-track {
  background: #3d3d3d;        /* цвет дорожки */
}
.list-section::-webkit-scrollbar-thumb {
  background-color: #ffd138;    /* цвет плашки */
  border-radius: 2px;       /* закругления плашки */
}